<template>
    <v-card>
        <v-form v-model="valid">
            <v-card-text>
              <v-icon color="primary" left>info</v-icon>Please enter your postcode below to check if we deliver to your address.
            </v-card-text>
              <v-divider></v-divider>
            <v-card-text>
              <v-row v-if="error">
                <v-col cols="12">
                    <v-icon leftc color="error" class="mr-2">announcement</v-icon>
                    <span class="error--text">{{errorMessage}}</span>
                  </v-chip>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="postcode"
                    outlined
                    prepend-inner-icon="home"
                    :rules="[required('PostCode'), isPostCode]"
                    label="PostCode"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-card-text>
            <v-card-actions>
              <v-btn
                block
                color="primary"
                x-large
                @click.stop="validatePostcode"
                :disabled="!valid"
              >Check Delivery Postcode</v-btn>
            </v-card-actions>
        </v-form>
    </v-card>
</template>
<script>
import checkPostCode from "@/util/validation/postcode.js";
import { mapActions } from "vuex";
export default {
  name: "PostcodeValidation",
  props:['nextStep'],
  data: () => {
    return {
      valid:false,
      postcode:'',
      required(fieldName) {
        return v => (v && v.length > 0) || `${fieldName} Required`;
      },
      isPostCode: val => {
        return checkPostCode(val);
      },
      error: false,
      errorMessage: "",
    };
  },
  methods: {
    ...mapActions(["checkDeliveryPostcode"]),
    validatePostcode() {
      const params = Object.assign(
        {},
        { identity: "nws9987" },
        { postcode: this.postcode }
      );
      this.error = false;
      this.checkDeliveryPostcode(params)
        .then(response => {
          if (response == "passed") {
            this.$emit('validated')
          }
        })
        .catch(msg => {
          this.error = true;
          this.errorMessage = msg;
        });
    }
  },
  computed: {},
  created(){
      this.postcode=''
      //this.$store.user.commit('setCncFlag',null)
  }
};
</script>