<template>
  <v-row class="pa-0" justify="center" align="center">
    <v-col cols="12" md="6" sm="12">
      <v-card>
        <v-toolbar color="primary" dark flat dense>
          <v-toolbar-title>User Registration</v-toolbar-title>
        </v-toolbar>
        <div class="py-0">
          <v-img :src="logo" class="my-3" contain height="140" />
        </div>
        <v-card-actions>
          <v-btn
            outlined
            block
            x-large
            color="primary"
            v-if="currentStep==1"
            @click.stop="$router.go(-1)"
          >
            <v-icon class="mr-2">arrow_back</v-icon>Back
          </v-btn>
          <v-btn outlined block x-large color="primary" @click.stop="previousStep" v-else>
            <v-icon class="mr-2">arrow_back</v-icon>Previous Step
          </v-btn>
        </v-card-actions>
      </v-card>
      <v-stepper v-model="currentStep">
        <v-stepper-header v-if="isCncOnly &&	isCncEnabled ">
          <v-stepper-step :complete="currentStep > 1" step="1">Postcode</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="currentStep > 2" step="2">Click & Collect</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="currentStep > 3" step="3">User Information</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="4">Delivery Information</v-stepper-step>
        </v-stepper-header>
        <v-stepper-header v-else>
          <v-stepper-step :complete="currentStep > 1" step="1">Postcode</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="currentStep > 2" step="2">User Information</v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step step="3">Delivery Information</v-stepper-step>
        </v-stepper-header>
        <v-stepper-items v-if="isCncOnly">
          <v-stepper-content step="1">
            <PostcodeValidation @validated="nextStep"></PostcodeValidation>
          </v-stepper-content>
          <v-stepper-content step="2">
            <ClickAndCollectOnly @valid-cnc-check="nextStep"></ClickAndCollectOnly>
          </v-stepper-content>
          <v-stepper-content step="3">
            <UserInformation
              :userDetails="userInfo"
              :error="userInfoError"
              :errorMessage="userInfoErrorMessage"
              @valid-user-info="nextStep"
            ></UserInformation>
          </v-stepper-content>
          <v-stepper-content step="4">
            <DeliveryInformation
              :deliveryAddress="deliveryInfo"
              :error="error"
              :errorMessage="errorMessage"
              :onRegister="completeRegistration"
            ></DeliveryInformation>
          </v-stepper-content>
        </v-stepper-items>
        <v-stepper-items v-else>
          <v-stepper-content step="1">
            <PostcodeValidation @validated="nextStep"></PostcodeValidation>
          </v-stepper-content>
          <v-stepper-content step="2">
            <UserInformation
              :userDetails="userInfo"
              :error="userInfoError"
              :errorMessage="userInfoErrorMessage"
              @valid-user-info="nextStep"
            ></UserInformation>
          </v-stepper-content>
          <v-stepper-content step="3">
            <DeliveryInformation
              :deliveryAddress="deliveryInfo"
              :error="error"
              :errorMessage="errorMessage"
              :onRegister="completeRegistration"
            ></DeliveryInformation>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-col>
  </v-row>
</template>
<script>
//import RegistrationForm from "@/components/auth/RegistrationForm.vue";
import PostcodeValidation from "@/components/auth/PostcodeCheckForm.vue";
import UserInformation from "@/components/auth/UserInfoForm.vue";
import DeliveryInformation from "@/components/auth/DeliveryInfoForm.vue";
import ClickAndCollectOnly from "@/components/auth/ClickAndCollectOnly.vue";
import { mapActions } from "vuex";
export default {
  name: "Registration",
  components: {
    PostcodeValidation,
    UserInformation,
    DeliveryInformation,
    ClickAndCollectOnly
  },
  data: () => {
    return {
      userInfo: {
        userName: "",
        email: "",
        telephone: "",
        password: "",
        confirmPassword: ""
      },
      deliveryInfo: {
        addressLine1: "",
        addressLine2: "",
        addressLine3: "",
        addressLine4: "",
        postcode: ""
      },
      userInfoError: false,
      userInfoErrorMessage: "",
      error: false,
      errorMessage: "",
      currentStep: 1
    };
  },
  methods: {
    ...mapActions(["registerNewCashUser", "setSnackbar"]),
    completeRegistration() {
      //make call to registerUserAction if response is success
      //redirect user to SignIn page
      //else flag the eroor
      this.error = false;
      this.errorMessage = "";
      this.userInfoError = false;
      this.userInfoErrorMessage = "";

      const rec = Object.assign(
        {},
        { identity: "nws9987" },
        { emailAddress: this.userInfo.email },
        { password: this.userInfo.password },
        { userName: this.userInfo.userName },
        { telephone: this.userInfo.telephone },
        this.deliveryInfo
      );
      this.registerNewCashUser(rec)
        .then(response => {
          if (response == "registration-complete") {
            this.$root.$emit("singup-success", this.userInfo.email);
            this.resetForm();
            this.setSnackbar({ text: "Thank You !.  Registration Successful" });
            this.$router.push({ name: "SignIn" });
            this.currentStep = 1;
          }
        })
        .catch(msg => {
          if (msg.includes("email address")) {
            this.previousStep();
            this.userInfoError = true;
            this.userInfoErrorMessage = msg;
          }
          this.error = true;
          this.errorMessage = msg;
          this.setSnackbar({ text: msg, color: "error" });
        });
    },
    nextStep() {
      this.currentStep = this.currentStep + 1;
    },
    previousStep() {
      this.currentStep = this.currentStep - 1;
    },
    resetForm() {
      this.userInfo.userName = "";
      this.userInfo.email = "";
      this.userInfo.telephone = "";
      this.userInfo.password = "";
      this.userInfo.confirmPassword = "";
      this.deliveryInfo.addressLine1 = "";
      this.deliveryInfo.addressLine2 = "";
      this.deliveryInfo.addressLine3 = "";
      this.deliveryInfo.addressLine4 = "";
      this.deliveryInfo.postcode = "";
    }
  },
  computed: {
    logo() {
      return require(process.env.VUE_APP_LOGO_MAIN);
    },
    isCncOnly() {
      return this.$store.state.user.cncOnly == "1";
    },
    isCncEnabled() {
      return this.$store.state.configs.orders_cnc_enabled == "1";
    }
  },
  created() {
    this.resetForm();
  }
};
</script>
<style scoped>
.v-stepper__content {
  padding-top: 16px;
  padding-right: 12px;
  padding-bottom: 16px;
  padding-left: 12px;
}
</style>
