<template>
  <v-row class="pa-0" justify="center" align="start">
    <v-col cols="12" md="8" sm="12">
      <GDPR :getSurveyData="getSurveyData"></GDPR>
      <v-img :src="logo" class="my-3" contain height="200" />
      <div
        :style="companyStyle"
        :class="{'display-2': $vuetify.breakpoint. smAndDown, 'display-3': $vuetify.breakpoint. md, 'display-4': $vuetify.breakpoint. lgAndUp}"
      >{{companyHomePageText}}</div>
    </v-col>
    <v-col :style="style" cols="12" md="12" sm="12">
      <v-row class="pa-0" justify="center" align="start">
        <v-col :style="style" cols="12" md="10" sm="12">
          <v-btn dark :to="{name: OrderHereLink}" block x-large :style="buttonStyle">Order Here</v-btn>
        </v-col>
      </v-row>
    </v-col>

    <v-col :style="style" cols="12" md="10" sm="12">
      <v-row class="pa-0" justify="space-between" align="start">
        <v-col :style="style" cols="12" md="6" sm="12">
          <v-card :loading="loadingCustomer" outlined>
            <div v-if="loadingCustomer">
              <v-card-title>Loading Customer</v-card-title>
            </div>
            <CustomerAddress v-else-if="retrieveCustomerAddress"></CustomerAddress>
          </v-card>
        </v-col>
        <v-col :style="style" cols="12" md="6" sm="12">
          <v-card :loading="loadingBasket" outlined>
            <div v-if="loadingBasket">
              <v-card-title>Loading Baskets</v-card-title>
            </div>
            <div v-else-if="trolly != false">
              <v-card-title>Current Basket</v-card-title>
              <v-card-text>Delivery Date: {{trolly.order_date_display}}</v-card-text>
              <v-card-subtitle>
                <div>
                  No. Items:
                  <b>{{trolly.totalItems}}</b>
                </div>
                <div>
                  Total Value:
                  <b>£{{trolly.totalValue}}</b>
                </div>
              </v-card-subtitle>
            </div>
            <div v-else>
              <v-card-title>No Baskets Open</v-card-title>
            </div>
            <v-card-actions v-if="trolly != false">
              <v-btn text color="blue darken-2" :to="{name: 'OrdersSearch'}">Continue Shopping</v-btn>
              <v-spacer />
              <v-btn text color="blue darken-2" :to="{name: 'OrdersBasket'}">View Basket</v-btn>
            </v-card-actions>
            <v-card-actions v-else>
              <v-spacer />
              <v-btn text color="blue darken-2" :to="{name: 'OrdersCreate'}">Start Shopping</v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
        <!-- <v-col :style="style" cols="12" md="4" sm="12">
          <v-card outlined>
            <div class="pa-4">
              <div v-if="retrieveCustomerAddress.name" style="padding: 1em 0">
                <strong>Last Order</strong>
              </div>

              <div style="padding: 1em 0"></div>
            </div>
          </v-card>
        </v-col>-->
      </v-row>
    </v-col>

    <v-snackbar multi-line v-model="unpaidSnackbar" :timeout="unpaidSnackbarTimeout">
      {{ unpaidSnackbarText }}
      <v-btn color="blue" text :to="{name: 'YourOrders'}">View</v-btn>
      <v-btn color="blue darken-2" text @click="unpaidSnackbar = false">Close</v-btn>
    </v-snackbar>
  </v-row>
</template>
<script>
import axios from "axios";

import { mapActions, mapGetters } from "vuex";
import CustomerAddress from "@/components/customer/CustomerAddress.vue";
import GDPR from "@/components/account/GDPR.vue";

export default {
  name: "Home",

  components: {
    GDPR,
    CustomerAddress
  },

  data: () => ({
    loadingCustomer: false,
    loadingBasket: true,
    getSurveyData: {},

    unpaidSnackbar: false,
    unpaidSnackbarText:
      "You have Unpaid Orders, to pay or void your unpaid orders go to Orders -> Your Orders",
    unpaidSnackbarTimeout: 5000
  }),

  beforeMount() {
    this.getTrollys()
      .then(response => {
        this.loadingBasket = false;
      })
      .catch(er => {
        console.log(er);
        this.loadingBasket = false;
      });
  },

  mounted() {
    this.checkAutoLoad();
  },

  activated() {
    this.checkUnpaid();
  },

  computed: {
    ...mapGetters(["retrieveCustomerAddress"]),

    logo() {
      return require(process.env.VUE_APP_LOGO_MAIN);
    },

    theme() {
      return this.$vuetify.theme.dark ? "dark" : "light";
    },
    style() {
      const Style = {};

      return Style;
    },
    buttonStyle() {
      const Style = {
        background: this.$vuetify.theme.themes[this.theme].primary
      };

      return Style;
    },
    companyHomePageText() {
      return this.$store.getters.retrieveCompanyHomePageText;
    },
    companyStyle() {
      const Style = {
        color: this.$vuetify.theme.themes[this.theme].primary,
        "text-align": "center"
      };

      return Style;
    },

    trolly() {
      return this.$store.getters.retrieveCurrentTrolly;
    },

    OrderHereLink() {
      if (this.trolly) {
        return "OrdersSearch";
      } else {
        return "OrdersCreate";
      }
    }
  },
  methods: {
    ...mapActions(["getCustomer", "getFAQ", "getTrollys"]),

    checkAutoLoad() {
      console.log("-- Checking Autoload");

      switch (
        parseInt(
          this.$store.getters.retrieveAllConfigs.orders_autoload_date_selection
        )
      ) {
        case 1:
          console.log("-- Autoload Date Selection");
          // if (this.$store.getters.retrieveCurrentTrolly) {
          //   this.$router.push({ name: "OrdersSearch" });
          // } else {
          //   this.$router.push({ name: "OrdersCreate" });
          // }

          this.$router.push({ name: "OrdersCreate" });
          break;
      }
    },

    async checkUnpaid() {
      if (
        this.$store.getters.retrieveIsCncCustomer ||
        this.$store.getters.retrieveIsCashCustomer ||
        this.$store.getters.retrieveIsPaymentCustomer
      ) {
        let payload = {
          userId: this.$store.getters.retrieveUser.userNum,
          sessionKey: this.$store.getters.retrieveUser.sessionKey,
          daysPast: 90,
          locked: 1
        };

        console.log("-- Checking Unpaid");
        console.log(payload);
        let stringPayload = JSON.stringify(payload);
        const params = new URLSearchParams();
        params.append("params", stringPayload);

        const response = await axios.post("get-orders", params);

        if (response.data.success == true) {
          if (response.data.orders.length > 0) {
            console.log("-- We have some locked Orders");
            console.log(response.data.orders);
            let showSnackbar = false;

            response.data.orders.forEach(order => {
              console.log(order);
              if (
                order.locked == 1 &&
                order.payment_found == true &&
                order.payment_success == "0"
              ) {
                //This is a failed Payment
                showSnackbar = true;
              } else if (
                order.locked == 1 &&
                order.payment_found == false &&
                order.payment_success == null
              ) {
                // This is an unpaid order
                showSnackbar = true;
              }
            });

            if (showSnackbar) {
              this.unpaidSnackbar = true;
            }
          }

          return response.data.success;
        } else {
          response.data.success;
        }
      }
    }
  }
};
</script>


<style lang="sass">
@import "~vuetify/src/styles/styles.sass"
</style>
